picture-element {
  width: 100%;
  height: auto;
  display: block;
}

picture-element figure {
  --aspect-ratio: 16 / 9;
  aspect-ratio: var(--aspect-ratio);
  overflow: hidden;
}

picture-element :where(figure, img) {
  line-height: 0;
}

picture-element :where(img) {
  object-fit: cover;
  width: 100%;
  height: 100%;
  display: block;
}

:where(slider-container, .swiper-wrapper, slider-item) {
  width: auto;
  height: 100%;
  display: block;
}

pre code.hljs {
  font-family: var(--font-mono);
  padding: 0;
}

.hljs {
  color: var(--text-200);
  background: none;
  padding: 0;
  line-height: 1.5;
  font-family: var(--font-mono) !important;
}

.hljs code {
  padding: 0;
}

.hljs-title {
  color: var(--success-900);
}

.hljs-variable {
  color: var(--accent-600);
}

.hljs-keyword {
  color: var(--accent-800);
}

.hljs-function {
  color: var(--accent-500);
}

.hljs-string {
  color: var(--danger-700);
}

.hljs-number {
  color: var(--warning-900);
}

.hljs-comment {
  color: var(--light-900);
}

.hljs-built_in {
  color: var(--info-800);
}

.hljs-literal {
  color: var(--danger-800);
}

.hljs-type {
  color: var(--blue-500);
}

.hljs-params {
  color: var(--accent-700);
}

.hljs-meta {
  color: var(--gray-600);
}

.hljs-meta-string {
  color: var(--pink-600);
}

.hljs-attribute, .hljs-selector-tag, .hljs-selector-id, .hljs-selector-class {
  color: #a6e22e;
}

.parse-markdown {
  --font-size: var(--font-sm);
  --spacing: 1em;
  --color: var(--gray-700);
  --background-color: var(--white);
  --accent-color: var(--blue-800);
}

.parse-markdown--styles-true {
  font-size: var(--font-size);
}

.parse-markdown--styles-true :where(h1) {
  font-size: var(--font-3xl);
  margin-top: var(--spacing);
  margin-bottom: calc(var(--spacing) / 2);
}

.parse-markdown--styles-true :where(h2) {
  font-size: var(--font-2xl);
  margin-top: var(--spacing);
  margin-bottom: calc(var(--spacing) / 2);
}

.parse-markdown--styles-true :where(h3) {
  font-size: var(--font-xl);
  margin-top: var(--spacing);
  margin-bottom: calc(var(--spacing) / 2);
}

.parse-markdown--styles-true :where(h4) {
  font-size: var(--font-lg);
  margin-top: var(--spacing);
  margin-bottom: calc(var(--spacing) / 2);
}

.parse-markdown--styles-true :where(h5) {
  font-size: var(--font-md);
  margin-top: var(--spacing);
  margin-bottom: calc(var(--spacing) / 2);
}

.parse-markdown--styles-true :where(h6) {
  font-size: var(--font-sm);
  margin-top: var(--spacing);
  margin-bottom: calc(var(--spacing) / 2);
}

.parse-markdown--styles-true :where(p) {
  margin-top: 0;
  margin-bottom: var(--spacing);
  font-size: 1em;
}

.parse-markdown--styles-true :where(ul) {
  margin-top: 0;
  margin-bottom: var(--spacing);
  font-size: 1em;
  list-style: none;
}

.parse-markdown--styles-true :where(ul) li:before {
  content: "•";
  margin-right: var(--spacing);
}

.parse-markdown--styles-true :where(ol) {
  font-size: var(--font-size);
  margin-top: 0;
  margin-bottom: var(--spacing);
  list-style: none;
}

.parse-markdown--styles-true :where(ol) li:before {
  content: counter(li) ". ";
  counter-increment: li;
}

.parse-markdown--styles-true :where(li) {
  font-size: var(--font-size);
  margin-top: 0;
  margin-bottom: calc(var(--spacing) / 2);
  margin-left: var(--spacing);
  position: relative;
}

.parse-markdown--styles-true :where(li):before {
  left: calc(var(--spacing) * -1);
  position: absolute;
}

.parse-markdown--styles-true :where(blockquote) {
  margin-top: 0;
  margin-bottom: var(--spacing);
  padding: calc(var(--spacing) / 2) var(--spacing);
  border-left: var(--px) solid var(--gray-300);
  background-color: var(--white);
}

.parse-markdown--styles-true :where(pre) {
  margin-top: 0;
  margin-bottom: var(--spacing);
  padding: calc(var(--spacing) / 1.5);
  border-bottom: var(--px) solid var(--gray-300);
  background-color: var(--gray-300);
  border-radius: var(--2);
  color: var(--black);
  font-size: var(--font-size);
}

.parse-markdown--styles-true :where(code) {
  border-radius: var(--relative-1);
  background-color: var(--gray-300);
  color: var(--black);
  padding: .165em;
  font-size: .8em;
}

.parse-markdown--styles-true :where(a) {
  color: var(--color-accent);
  text-decoration: underline;
}

.parse-markdown--styles-true :where(img) {
  max-width: 100%;
  height: auto;
}

icon-element {
  fill: currentColor;
  vertical-align: middle;
  display: block;
}

